@keyframes pulsate {
  0% {
    transform: scale(1);
    transform-origin: center center;
    animation-timing-function: ease-out;
  }
  10% {
    transform: scale(0.91);
    animation-timing-function: ease-in;
  }
  17% {
    transform: scale(0.98);
    animation-timing-function: ease-out;
  }
  33% {
    transform: scale(0.87);
    animation-timing-function: ease-in;
  }
  45% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }
}
.mask-group-1 {
  width: 24px;
  position: relative;
  border-radius: var(--br-9xs);
  height: 24px;
  object-fit: cover;
  cursor: pointer;
}
.playsatoshisquest {
  position: relative;
  line-height: 15px;
}
.mask-group-1-parent {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 0 16px;
}
.hamburger-menu-v5-child,
.hamburger-menu-v5-item {
  width: 31.8px;
  position: relative;
  border-radius: var(--br-31xl);
  height: 4.2px;
}
.hamburger-menu-v5-item {
  height: 3.6px;
}
.hamburger-menu-v5 {
  height: 24px;
  display: none;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}
.hamburger-menu-v5:active {
  cursor: auto;
  animation: 1s ease-out 0s 1 normal none pulsate;
  opacity: 1;
}
.map {
  position: relative;
  line-height: 15px;
  cursor: pointer;
}
.desktop-menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 0 20px;
}
.icon-game-controller {
  width: 24.5px;
  position: relative;
  height: 18px;
}
.connect-button {
  border-radius: var(--br-9xs);
  background-color: var(--color-mediumseagreen);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-7xs) var(--padding-5xs);
}
.navigation,
.top-menu {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: right;
  color: var(--color-white);
}
.navigation {
  height: 40px;
  width: 317px;
  justify-content: flex-end;
  gap: 0 20px;
  font-size: var(--font-size-xl);
  font-family: var(--font-open-sans);
}
.top-menu {
  align-self: stretch;
  background-color: var(--color-gray-100);
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-13xl);
  font-size: var(--text-lg-regular-size);
  font-family: var(--font-roboto-slab);
}
.connect-play,
.open-your-minecraft {
  align-self: stretch;
  position: relative;
}
.connect-play {
  line-height: 44px;
  font-weight: 500;
}
.open-your-minecraft {
  font-size: var(--text-lg-regular-size);
  line-height: 24px;
  font-family: var(--font-open-sans);
}
.connect-play-parent {
  width: 100%;
  border-radius: var(--br-5xs);
  background-color: rgba(6, 142, 53, 0.81);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-base);
  box-sizing: border-box;
  gap: var(--gap-base);
  max-width: 883px;
}
.picture-icon {
  width: 883px;
  position: relative;
  border-radius: var(--br-5xs);
  height: 497px;
  object-fit: cover;
}
.picture-wrapper {
  align-self: stretch;
  border-radius: var(--br-5xs);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.wimmer-loves-alex {
  position: relative;
  line-height: 24px;
  font-weight: 600;
}
.wimmer-loves-alex-ramey-gaming-wrapper {
  align-self: stretch;
  background-color: #ffefef;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-base);
  text-align: left;
  font-size: var(--font-size-xl);
  color: #393939;
  font-family: var(--font-open-sans);
}
.body {
  align-self: stretch;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-13xl) var(--padding-45xl);
  gap: 32px 0;
}
.satoshis-quest {
  position: relative;
  line-height: 15px;
  font-weight: 600;
}
.playsatoshisquest1 {
  height: 15px;
  position: relative;
  font-size: var(--font-size-base);
  line-height: 15px;
  font-family: var(--font-roboto);
  color: var(--color-lightgray);
  display: inline-block;
}
.satoshis-quest-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 6px 0;
}
.bottom-menu-inner {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.vector-icon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.icon-discord {
  text-decoration: none;
  width: 21.7px;
  position: relative;
  height: 24px;
}
.icon-facebook,
.icon-instagram,
.icon-twitter {
  width: 24px;
  position: relative;
  height: 24px;
}
.icon-facebook,
.icon-twitter {
  width: 24.1px;
}
.icon-twitter {
  width: 29.5px;
}
.navigation1 {
  margin: 0;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 0 32px;
}
.bottom-menu,
.home {
  display: flex;
  align-items: center;
  font-family: var(--font-roboto-slab);
}
.bottom-menu {
  margin: 0;
  align-self: stretch;
  background-color: var(--color-gray-100);
  flex-direction: row;
  justify-content: space-between;
  padding: var(--padding-13xl);
  text-align: right;
  font-size: var(--font-size-xl);
  color: var(--color-white);
}
.home {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  font-size: 36px;
  color: var(--color-whitesmoke-200);
}
@media screen and (max-width: 1200px) {
  .navigation {
    display: flex;
  }
  .bottom-menu,
  .top-menu {
    padding-left: var(--padding-45xl);
    padding-right: var(--padding-45xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .hamburger-menu-v5,
  .playsatoshisquest {
    display: flex;
  }
  .desktop-menu,
  .navigation {
    display: none;
  }
  .body,
  .top-menu {
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
  .body {
    padding-top: var(--padding-13xl);
  }
  .playsatoshisquest1,
  .satoshis-quest {
    display: flex;
  }
  .bottom-menu {
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .desktop-menu,
  .playsatoshisquest {
    display: none;
  }
  .hamburger-menu-v5:active {
    animation: 1s ease-in 0s 1 normal none pulsate;
    opacity: 1;
  }
  @keyframes pulsate {
    0% {
      transform: scale(1);
      transform-origin: center center;
      animation-timing-function: ease-out;
    }
    10% {
      transform: scale(0.91);
      animation-timing-function: ease-in;
    }
    17% {
      transform: scale(0.98);
      animation-timing-function: ease-out;
    }
    33% {
      transform: scale(0.87);
      animation-timing-function: ease-in;
    }
    45% {
      transform: scale(1);
      animation-timing-function: ease-out;
    }
  }
  .body,
  .top-menu {
    padding-left: var(--padding-base);
    padding-right: var(--padding-base);
    box-sizing: border-box;
  }
  .body {
    padding-top: var(--padding-13xl);
  }
  .playsatoshisquest1,
  .satoshis-quest {
    display: flex;
  }
  .bottom-menu-inner {
    display: none;
  }
  .navigation1 {
    display: flex;
  }
  .bottom-menu {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding-left: var(--padding-base);
    padding-right: var(--padding-base);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .body {
    padding-left: var(--padding-base);
    padding-top: var(--padding-13xl);
    padding-right: var(--padding-base);
    box-sizing: border-box;
  }
}
